.header {
    background-color: #090413e5;
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 100;
}
.header__logo {
    width: 100px;
    margin-top: -20px;
    margin-left: 10px;
    margin-bottom:-20px;
    object-fit: contain;
}
.header__search {
    display: flex;
    flex: 1;
}
.header__searchInput {
    height: 12px;
    padding: 10px;
    border: none;
    width: 100%;
}
.header__searchIcon {
    background-color: rgb(184, 172, 172);
    padding: 4px;
}
.header__nav {
    display: flex;
    justify-content: space-evenly;
}
.header__link {
    color: white;
    text-decoration: none;
}
.header__option {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
}
.header__option1 {
    font-size: 10px;

}
.header__option2 {
    font-size: 13px;
    font-weight:800;
}
.header__optionBasket {
    display: flex;
    align-items: center;
}
.header__basketCount {
    margin-left: 10px;
    margin-right:10px;
}